export class Navbar extends Component {

  render() {
    return (
      <div className="navbar">

        <div className="title">
          <img src="logo_2.png" className="App-logo" alt="logo" />
        </div>

        <ul className="main">
            <li><NavLink to="/signature-ui/" className="link">Getting Started</NavLink></li>
            <li><NavLink to="/signature-ui/about" className="link">About Signature UI</NavLink></li>
          <li><NavLink to="/signature-ui/library" className="link">Signature UI</NavLink>

              <ul className="submenu">
                  <li><a href="/signature-ui/library#textbutton">Text Button</a></li>
                  <li><a href="/signature-ui/library#iconbutton">Icon Button</a></li>
                  <li><a href="/signature-ui/library#textinput">Text Input</a></li>
                  <li><a href="/signature-ui/library#radio">Radio</a></li>
                  <li><a href="/signature-ui/library#checkbox">Checkbox</a></li>
                  <li><a href="/signature-ui/library#dropdown">Dropdown</a></li>
                  <li><a href="/signature-ui/library#combodropdown">Combo Dropdown</a></li>
                  <li><a href="/signature-ui/library#multiselect">Mulitselect</a></li>
                  <li><a href="/signature-ui/library#multiselectsearch">Multiselect Search</a></li>

              </ul>

              {/*         <li><NavLink to="/signature-ui/library#textbutton" className="link">Text Button</NavLink></li>
               <li><NavLink to="/signature-ui/library#iconbutton" className="link">Icon Button</NavLink></li>
               <li><NavLink to="/signature-ui/library#textinput" className="link">Text Input</NavLink></li>
               <li><NavLink to="/signature-ui/library#radio" className="link">Radio</NavLink></li>
               <li><NavLink to="/signature-ui/library#checkbox" className="link">Checkbox</NavLink></li>
               <li><NavLink to="/signature-ui/library#dropdown" className="link">Dropdown</NavLink></li>
               <li><NavLink to="/signature-ui/library#combodropdown" className="link">Combodropdown</NavLink></li>
               <li><NavLink to="/signature-ui/library#multiselect" className="link">Multisearch</NavLink></li>
               <li><NavLink to="/signature-ui/library#multiselectsearch" className="link">Multiselect Search</NavLink></li>*/}

          </li>
            <li>
                <ul className="submenu2">
                    <li>Follow us on
                        <a href="https://www.facebook.com/GrondaGastro/" target="blank" className="social-icon"><FaFacebookOfficial/></a>
                        <a href="https://medium.com/gronda" target="blank" className="social-icon"><FaMedium/></a>
                        <a href="https://www.instagram.com/gronda.eu" target="blank" className="social-icon"><FaInstagram/></a>
                    </li>

                    <li>&copy; Gronda 2018</li>
                </ul>
            </li>
        </ul>





      </div>
    );
  }
}

export default Navbar;



